<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from 'echarts';

  export default {
    name: "HorizontalBar",
    props: {
      animateFlag: Boolean,
      params: {
        type: Object,
        default() {
          return {
            canvasStyle: {
              width: "100%",
              height: "100%"
            },
            options: {
              rollFlag: false, //tootil是否自动轮播默认不是自动轮播
            },
            data: []
          };
        }
      }
    },
    data() {
      return {
        myChart: null,
        rollFlag: false
      };
    },
    mounted() {
      this.init();
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;
        this.myChart = echarts.init(this.$refs.chartEl);
        let propData = JSON.parse(JSON.stringify(this.params.data));
        //数据处理
        let propDataBg = [], scatterArr = [], dataValue = [], dataName = [], dataMax = 0, total = 0;

        propData.forEach(item => {
          total += item.value;
        });

        // 数据全为0时，数据顺序和初始顺序反转（排序失效）
        if (total === 0) {
          propData = [...propData].reverse();
        }
        
        propData.forEach(item => {
          dataName.push(item.name);
          dataValue.push(item.value);
          dataMax = (item.value > dataMax ? item.value : dataMax);
          total += item.value;
        });
        
        propData.forEach(item => {
          let obj1 = {
            "name": item.name,
            "value": dataMax * 1.1,
            itemStyle: {
              borderColor: "#117cbb",
            },
          };
          propDataBg.push(obj1);
          
          let obj2 = {
            "name": item.name,
            "value": 0
          };
          scatterArr.push(obj2);
        });
        
        // 默认高亮
        // let highlightIndex = this.params.options && this.params.options.highlightIndex;
        // if (highlightIndex || highlightIndex === 0) {
        //   propData[highlightIndex].itemStyle = {
        //     color: '#f5e752'
        //   };
        //   propDataBg[highlightIndex].itemStyle = {
        //     borderColor: '#f5e752'
        //   };
        //   scatterArr[highlightIndex].itemStyle = {
        //     color: '#f5e752'
        //   };
        // }
        this.option = {
          grid: {
            left: '10%',
            top: '0%',
            right: '10%',
            bottom: '0%',
            containLabel: true
          },
          xAxis: [
            {
              show: false,
            }
          ],
          yAxis: [
            {
              axisTick: 'none',
              axisLine: 'none',
              offset: '20',
              axisLabel: {
                color: '#fff',
                fontSize: 16,
                formatter: (params) => {
                  // 标签文字长度超过4时，...显示
                  if (params.length > 4) {
                    return params.slice(0, 6) + '...'
                  } else {
                    return params
                  }
                },
              },
              data: dataName
            },
            {
              axisTick: 'none',
              axisLine: 'none',
              axisLabel: {
                textStyle: {
                  color: '#ffffff',
                  fontSize: '16',
                }
              },
              data: dataValue
            },
            {
              name: '',
              nameGap: '50',
              nameTextStyle: {
                color: '#ffffff',
                fontSize: '16',
              },
              axisLine: {
                lineStyle: {
                  color: 'rgba(0,0,0,0)'
                }
              },
              data: [],
            }
          ],
          series: [
            {
              name: '条',
              type: 'bar',
              yAxisIndex: 0,
              data: propData,
              label: {
                normal: {
                  show: false,
                  position: 'right',
                  textStyle: {
                    color: '#ffffff',
                    fontSize: '16',
                  },
                }
              },
              barWidth: 11,
              itemStyle: {
                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
                  offset: 0,
                  color: '#016AF5'
                }, {
                  offset: 1,
                  color: '#81E3FF'
                }]),
                barBorderRadius: 20
              },
              z: 2
            },
            {
              name: 'bar背景',
              type: 'bar',
              yAxisIndex: 1,
              barGap: '-100%',
              barWidth: 12,
              itemStyle: {
                color: 'transparent',
                borderWidth: 2,
                barBorderRadius: 5,
              },
              data: propDataBg,
              
              z: 1
            },
          ],
        };
        this.myChart.setOption(this.option);

        //柱图点击事件
        // let curName = '';
        // this.myChart.on("click", params => {
        //   if (this.params.options && this.params.options.click) {
        //     curName = (curName === params.name ? '' : params.name);

        //     this.option.series[0].data.forEach(el => {
        //       el.itemStyle = {};
        //       if (el.name === curName) {
        //         el.itemStyle.color = "#f5e752"
        //       } else {
        //         el.itemStyle.color = new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
        //           offset: 0,
        //           color: '#4cbfef'
        //         }, {
        //           offset: 1,
        //           color: '#fdaa33'
        //         }])
        //       }
        //     });

        //     this.option.series[1].data.forEach(el => {
        //       el.itemStyle = {};
        //       if (el.name === curName) {
        //         el.itemStyle.borderColor = '#f5e752'
        //       } else {
        //         el.itemStyle.borderColor = '#117cbb'
        //       }
        //     });
            
        //     // this.option.series[2].data.forEach(el => {
        //     //   el.itemStyle = {};
        //     //   if (el.name === curName) {
        //     //     el.itemStyle.color = '#f5e752'
        //     //   } else {
        //     //     el.itemStyle.color = '#4cbfef'
        //     //   }
        //     // });

        //     this.myChart.setOption(this.option);
        //     this.$emit("dwpmClick", curName);
        //   }
        // });
      },
    },
    computed: {
      styleType() {
        return this.$store.state.styleType;
      },
      realData() {
        return this.params.data
      }
    },
    watch: {
      realData: {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

<style lang="stylus" scoped>
</style>
